.post {
    position: relative;
    background-image: url(https://images.unsplash.com/photo-1501820488136-72669149e0d4?ixid=MXwxMjA3fDB8MHxzZWFyY2h8OHx8Y2F0fGVufDB8fDB8&ixlib=rb-1.2.1&auto=format&fit=crop&w=800&q=60);
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 225px;
    height: 225px;
    transition: all 0.3s ease-in-out;
    &:after {
        position: absolute;
        display: block;
        content: "";
        bottom: 0;
        left: 0;
        right: 0;
        width: 100%;
        height: 0px;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, #FFFFFF 58.85%);
        transition: all 0.2s ease-in;
      }
    &:hover {
        transform: scale(1.14);
        box-shadow: 0px 6px 5px rgba(0, 0, 0, 0.24), 0px 9px 18px rgba(0, 0, 0, 0.18);
      }
    &:hover .like-button {
        opacity: 1;
      }
    &:hover:after{
        height: 94px;
      }
  }