@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Neucha&display=swap);
*{box-sizing:border-box}body{margin:0;font-family:'Roboto', sans-serif;font-style:normal;font-weight:400;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-color:#21cc99}


.header{padding-left:62px;background-color:#f39121a9;box-shadow:0px 4px 4px rgba(0,0,0,0.24);border-radius:20px}

.nav{min-height:64px;display:-webkit-flex;display:flex;margin:0;padding:0;list-style:none}

.nav__item{padding:0 18px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;font-size:14px;line-height:1.5;text-align:center;letter-spacing:0.25px;color:rgba(255,255,255,0.7);border:none;background-color:#f39121a9;transition:all 0.3s ease-in-out;border-radius:0 0px 20px 20px;cursor:pointer}.nav__item:hover{background:#34dba9}a{text-decoration:none}


.posts-grid{margin:48px auto 48px;padding:0;width:calc(100% - 124px);display:grid;grid-template-columns:repeat(auto-fill, 225px);grid-gap:48px;gap:48px;-webkit-justify-content:space-between;justify-content:space-between;list-style:none}

.like-button{position:absolute;bottom:11.04%;right:8.33%;opacity:0;padding:0;width:40px;height:36.7px;outline:none;border:none;background-color:transparent;background-image:url(/Cat-Pinterest/static/media/border_heart.6adfe85f.svg);background-repeat:no-repeat;transition:all 0.3s ease-in-out;z-index:2;cursor:pointer}.like-button:hover{background-image:url(/Cat-Pinterest/static/media/hovered_heart.8220379c.svg)}._liked{background-image:url(/Cat-Pinterest/static/media/filled_heart.8220379c.svg)}

.post{position:relative;background-image:url(https://images.unsplash.com/photo-1501820488136-72669149e0d4?ixid=MXwxMjA3fDB8MHxzZWFyY2h8OHx8Y2F0fGVufDB8fDB8&ixlib=rb-1.2.1&auto=format&fit=crop&w=800&q=60);background-position:center;background-repeat:no-repeat;background-size:cover;width:225px;height:225px;transition:all 0.3s ease-in-out}.post:after{position:absolute;display:block;content:"";bottom:0;left:0;right:0;width:100%;height:0px;background:linear-gradient(180deg, rgba(255,255,255,0) 0%, #fff 58.85%);transition:all 0.2s ease-in}.post:hover{-webkit-transform:scale(1.14);transform:scale(1.14);box-shadow:0px 6px 5px rgba(0,0,0,0.24),0px 9px 18px rgba(0,0,0,0.18)}.post:hover .like-button{opacity:1}.post:hover:after{height:94px}

.no-cats{width:100%;height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}.no-cats-img{z-index:0;width:500px;height:500px}.no-cats-title{z-index:999;position:relative;font-family:'Neucha', cursive;font-size:60px}

